.links{
	margin: 10px;
	padding: 10px;
	font-size: 18px;
	box-sizing: border-box;
}
.links:hover{
  cursor: pointer;
}
.link-list:hover{
    animation-name: zoom;
    animation-duration: 1s;
}
@keyframes zoom{
	50%{font-size: 25px;}
}

.ziggle{
	animation-name: ziggle;
	animation-duration: 3s;
	animation-iteration-count: 3;
}
@keyframes ziggle{
	0%{margin-left: 0px;}
	50%{margin-top: 50px;margin-left: 20px;}
	100%{margin-left:50px; }
}